











































.no_address {
	width: 430px;
	background: #fff;
	margin-top: 20%;
	text-align: left;
	
	.header_win {
		height: 250px;
		background: url(/local/dist/images/r_bottom_border_w.png) repeat-x left bottom #ffde00;
		display: block;
	}
	
	.no_address_icon {
		background: url(/local/dist/images/no_address_i.png) no-repeat center center;
		width: 130px;
		height: 130px;
		margin: 0 auto;
		position: relative;
		top: 50px;
	}
	
	.content {
		min-height: 200px;
	}
	
	h2 {
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 15px !important;
		padding-bottom: 15px !important;
		margin-top: 0 !important;
	}
	
	p {
		font-size: 95%;
	}
	
	.field_submit_wrap {
		display: flex;
		margin-top: 20px;
		justify-content: space-between;
		
		a {
			color: #4bce5b;
		}
	}
	
	.lk__add-address .b-button {
		width: auto;
		margin-left: 0;
	}
}

.b-button__close-popup {
	right: auto;
	top: 12%;
	left: 450px;
	
}



