




















































































































































































































































































































































.profile_user {
	padding-top: 60px;
	padding-bottom: 30px;
	
	&-title {
		display: flex;
		margin-bottom: 15px;
		
		.username {
			font-size: 36px;
			font-family: 'HelveticaNeueCyr-Thin';
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
		
	}
	
	.avatar {
		padding-right: 20px;
		
		.fa {
			font-size: 130px;
		}
		
		img {
			border: 1px solid #fff;
			border-radius: 50%;
		}
	}
	
	&-password {
	
	}
	
	&-main {
	
	}
	
	.title_block {
		color: #898989;
		font-size: 13px;
		display: inline-block;
		margin-bottom: 15px;
	}
	
	.form_item {
		margin: 10px 0;
		display: flex;
		align-items: center;
		
		label {
			width: 125px;
		}
		
		.form__input_middle {
			width: 335px;
		}
		
		.grey-select {
			width: 160px;
			background: #f0efec;
			border: none;
			height: 50px;
			border-radius: 30px;
			
			.el-input, .el-input__inner {
				background: transparent;
				border: none;
				border-radius: 30px;
				font-size: 18px;
				padding: 5px 10px;
				font-family: 'HelveticaNeueCyr-Thin';
			}
		}
		
		.form__input_xs {
			margin-right: 0;
		}
		
		.save_btn {
			width: 335px;
			text-align: left;
		}
	}
	
	.date_field {
		display: flex;
		justify-content: space-between;
		width: 335px;
	}
	
	.profile_popup_wrap {
		.modal_content {
			justify-content: center;
			align-items: center;
			position: fixed;
		}
		
		.cart__prod-title .fa {
			font-size: 7rem;
			color: #fff;
		}
	}
	
	.fake_field {
		.form__input {
			width: auto;
			background: #f0efec;
			border: none;
			padding: 15px 20px;
			-webkit-border-radius: 30px;
			border-radius: 30px;
			font-size: 18px;
			color: #808080;
			font-family: 'HelveticaNeueCyr-Thin';
		}
		
	}
}



