$border: 1px solid $borderColor;
$greenColor: #34c263;
$yellowColor: #f8be00;
$redColor: #d60000;

.order_detail_wrap {
	height: 100vh;
}

.order_detail {
	padding-top: 42px;
	padding-bottom: 30px;
	
	h1 {
		font-size: 18px;
		font-family: 'HelveticaNeueCyr-Thin';
		padding-bottom: 30px;
	}
	
	&-delivery {
		font-size: 15px;
		color: #898989;
		line-height: 22px;
		margin: 10px 0;
	}
	
	&-body {
		display: flex;
	}
	
	.order-left {
		width: 64%;
		padding-right: 15px;
	}
	.order-right {
		flex: 1;
	}
	
	&-item_wrap {
		margin-bottom: 50px;
		border-bottom: $border
	}
	
	&-basket {
		margin: 10px 0;
		padding-top: 10px;
		
		&--item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 25px;
			
			div {
				margin-right: 15px;
			}
		}
	}
	.el-collapse {
		border: none;
		
		.el-collapse-item__header {
			line-height: 30px;
			height: auto;
		}
		
		.el-collapse-item__arrow {
			line-height: 30px;
			float: left;
		}
	}
	.basket-image {
		width: 100px;
		margin-right: 25px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	.basket-quantity {
		width: 75px;
		/*text-align: center;*/
	}
	
	.basket-price {
		width: 70px;
		text-align: right;
	}
	
	.basket-info {
		flex: 1;
		margin-right: 25px;
	}
	
	.b-popup-chekc_lk {
		margin-left: 0;
		padding-left: 30px;
	}
	.comment__text {
		line-height: 18px;
	}
	
	.basket_comment {
		color: #898989;
		font-size: 85%;
		
		p {
			margin: 6px 0;
		}
		
		.title_comment {
			margin: 0;
			padding: 15px 0 0 0;
			font-weight: bold;
		}
	}
	
	.replaces_from {
		text-decoration: line-through;
	}
	
	.basket__title {
		margin-top: 25px;
		font-size: 18px;
		
		.fas, .far {
			color: #fff;
			border-radius: 50%;
			padding: 4px;
			font-size: 14px;
		}
		
		.fa-check {
			background-color: $greenColor;
		}
		.fa-reply {
			background-color: $redColor;
		}
		.fa-sync-alt {
			background-color: $yellowColor;
		}
		
		.fa-clock {
			background-color: #898989;
		}
	}
	
	.basket__wrap {
		border-top: 2px dashed $borderColor;
	}
	
	.check__total {
		margin-bottom: 10px;
	}
}



