
































































































.favorite-product {
	width: 48%;
	padding: 10px 20px;
	display: flex;
	flex-direction: column;
	
	.separ {
		height: 1px;
		width: 100%;
		background: #e5e5e5;
		border: none;
		margin-bottom: 3px;
	}
	
	.product_body {
		display: flex;
		width: 100%;
		padding: 15px;
		transition: box-shadow 0.2s ease-out;
		border-radius: 5px;
		min-height: 120px;
		
		&:hover {
			box-shadow: inset 0 0 0 3px #fdd000;
		}
		
		.image {
			width: 80px;
			margin-right: 15px;
			/*display: flex;
			flex-direction: column;
			justify-content: center;*/
		}
		.name {
			padding-top: 10px;
			min-height: 55px;
			font-size: 14px;
			color: #000;
			line-height: 1.1em;
			width: 70%;
			
			input {
				display: inline-block;
				margin-right: 10px;
			}
			
			label {
				display: flex;
				cursor: pointer;
			}
		}
		
		.star_link {
			background-image: url(/local/dist/images/sprite2.png);
			background-position: -575px -618px;
			width: 20px;
			height: 19px;
			display: inline-block;
			opacity: 1;
			-ms-filter: none;
			filter: none;
		}
		
		.star_link.star_null {
			background-position: -530px -618px !important;
		}
	}
	
	.price {
		margin-bottom: 20px;
		
		.price_val {
			padding-top: 12px;
			font-size: 24px;
			font-family: 'HelveticaNeueCyr-Thin';
			margin-left: 25px;
		}
	}
	
	.basket_action {
		display: flex;
		.add_basket_btn {
			width: auto !important;
		}
	}
	
}



