




























































































.favorites {
	padding-top: 42px;
	padding-bottom: 30px;
	
	h1 {
		font-size: 30px;
		font-family: 'HelveticaNeueCyr-Thin';
		padding-bottom: 20px;
	}
	
	&-desc {
		font-size: 1.1rem;
		
		span {
			font-size: 0.9rem;
			color: #898989;
			display: block;
		}
	}
	
	&-create_list {
		margin-top: 40px;
	}
	
	.create_list-form_list {
		display: flex;
		align-items: center;
		padding-top: 20px;
	}
	.create_list-title {
		margin-right: 20px;
	}
	
	.favorites-list {
		padding: 0;
		margin: 20px 0 0 0;
		
		.lk__lists__item .del_section {
			display: none;
			color: rgba(176, 0, 0, 0.74);
			margin-left: 5px;
			cursor: pointer;
		}
		
		.lk__lists__item:hover > .del_section {
			display: inline-block;
			color: rgba(0, 0, 0, 0.6);
		}
	}
}



